import React from 'react';

const styles = {

    marginLeft: '50px', 
    marginTop: '50px',
    height: '300px', 
    width: '400px',
    paddingTop: '50px',
    paddingLeft: '50px',
    paddingRight: '50px',
    paddingBottom: '50px',
    backgroundColor: 'rgb(242, 248, 253)',
}

const ContainerDecorator = storyFn => <div style={styles}>{ storyFn() }</div>;

export { ContainerDecorator }